/**
 * WebReferenceCard 
 * 
 */

import { memo, useCallback } from 'react';

import type { WebReference } from '../messages/types';

import buildClassName from '../../../util/buildClassName';

import styles from './WebReferenceCard.module.scss';

interface OwnProps {
  reference: WebReference;
  onOpen?: (url: string) => void;
  className?: string;
}

const WebReferenceCard = ({
  reference,
  onOpen,
  className,
}: OwnProps) => {
  const {
    url, title, description, favicon, publishedTime,
  } = reference;

  const handleClick = useCallback(() => {
    if (onOpen) {
      onOpen(url);
    } else {
      window.open(url, '_blank', 'noopener,noreferrer');
    }
  }, [url, onOpen]);

  const displayTitle = title || url;
  const hostname = new URL(url).hostname;

  return (
    <div
      className={buildClassName(styles.root, className)}
      onClick={handleClick}
    >
      <div className={styles.header}>
        {favicon ? (
          <img src={favicon} alt="" className={styles.favicon} />
        ) : (
          <span className={styles.icon}></span>
        )}
        <div className={styles.meta}>
          <div className={styles.title}>{displayTitle}</div>
          {publishedTime && (
            <div className={styles.publishedTime}>{publishedTime}</div>
          )}
        </div>
      </div>

      {description && (
        <div className={styles.description}>
          {description}
        </div>
      )}

      <div className={styles.url}>{hostname}</div>
    </div>
  );
};

export default memo(WebReferenceCard);
